<template>
  <h1 style="margin: 20px">成员展示</h1>
  <a-row>
    <a-col :span="12">
      <a-card class="infoCard" v-for="item in leftMem" :key="item.id">
        <a-row class="userInfo">
          <!--头像-->
          <a-col :span="4" style="min-height: 100px">
            <div style="place-items: center; display: grid">
              <a-avatar :size="130">
                <img alt="avatar" :src="item.avatar" />
              </a-avatar>
            </div>
          </a-col>
          <!--用户基础信息-->
          <a-col :span="10" style="min-height: 4rem; margin-left: 60px">
            <!--用户名-->
            <a-row>
              <h2 class="nickName">{{ item.id }}</h2>
            </a-row>
            <!--分割-->
            <div style="margin: 16px 0"></div>
            <!--一句话描述-->
            <a-row class="member-description">
              <div>{{ item.des }}</div>
            </a-row>
            <!--分割-->
            <div style="margin: 16px 0"></div>
            <!--标签-->
            <a-tag
              v-for="tag in item.tags"
              :key="tag"
              color="green"
              style="margin-right: 10px"
              >{{ tag }}
            </a-tag>
          </a-col>
        </a-row>
        <template #actions>
          <span class="icon-hover" @click="toUrl(item.gitHub)">
            <IconGithub />
          </span>
          <span class="icon-hover" @click="toUrl(item.blogSite)">
            <IconLaunch />
          </span>
        </template>
      </a-card>
    </a-col>
    <a-col :span="12">
      <a-card class="infoCard" v-for="item in rightMem" :key="item.id">
        <a-row class="userInfo">
          <!--头像-->
          <a-col :span="4" style="min-height: 100px">
            <div style="place-items: center; display: grid">
              <a-avatar :size="130">
                <img alt="avatar" :src="item.avatar" />
              </a-avatar>
            </div>
          </a-col>
          <!--用户基础信息-->
          <a-col :span="10" style="min-height: 4rem; margin-left: 60px">
            <!--用户名-->
            <a-row>
              <h2 class="nickName">{{ item.id }}</h2>
            </a-row>
            <!--分割-->
            <div style="margin: 16px 0"></div>
            <!--一句话描述-->
            <a-row class="member-description">
              <div>{{ item.des }}</div>
            </a-row>
            <!--分割-->
            <div style="margin: 16px 0"></div>
            <!--标签-->
            <a-tag
              v-for="tag in item.tags"
              :key="tag"
              color="green"
              style="margin-right: 10px"
              >{{ tag }}
            </a-tag>
          </a-col>
        </a-row>
        <template #actions>
          <span class="icon-hover" @click="toUrl(item.gitHub)">
            <IconGithub />
          </span>
          <span class="icon-hover" @click="toUrl(item.blogSite)">
            <IconLaunch />
          </span>
        </template>
      </a-card>
    </a-col>
  </a-row>
</template>

<script setup>
import { IconGithub, IconLaunch } from "@arco-design/web-vue/es/icon";
import { onMounted, ref } from "vue";

const membersInfo = ref([]);
const leftMem = ref([]);
const rightMem = ref([]);
const loadData = async () => {
  let fileList = [];
  await fetch("/team-profile/members/total.json")
    .then((response) => response.json())
    .then((res) => {
      fileList = res["list"];
    })
    .catch((err) => {
      console.log("err", err);
    });
  for (const path of fileList) {
    const realPath = "/team-profile/members/" + path;
    await fetch(realPath)
      .then((response) => response.json())
      .then((res) => {
        membersInfo.value.push(res);
      })
      .catch((err) => {
        console.log("err", err);
      });
  }
  let i = 0;
  for (const mem of membersInfo.value) {
    if (i % 2 === 0) {
      leftMem.value.push(mem);
    } else {
      rightMem.value.push(mem);
    }
    i += 1;
  }
};
onMounted(() => {
  loadData();
});
const toUrl = (url) => {
  return window.open(url, "_blank");
};
</script>
